<template>
  <div class="m-tinymce">
	<textarea :id="id"></textarea>
	<!--  -->
	<input ref="imgFile" type="file" :accept="format" hidden @change="selectFile"/>
  </div>
</template>

<script type="text/ecmascript-6">
import "tinymce/tinymce";
import "../../assets/js/tinymce-langs/zh_CN";
import '../../assets/js/tinymce-plugins/placeholder/plugin.js'
import "tinymce/themes/modern/theme";
//import 'tinymce/skins/lightgray/skin.min.css'
//import 'tinymce/skins/lightgray/content.min.css'
import "tinymce/plugins/advlist/plugin.js";
import "tinymce/plugins/autolink/plugin.js";
import "tinymce/plugins/lists/plugin.js";
import "tinymce/plugins/link/plugin.js";
import "tinymce/plugins/image/plugin.js";
import "tinymce/plugins/charmap/plugin.js";
import "tinymce/plugins/print/plugin.js";
import "tinymce/plugins/preview/plugin.js";
import "tinymce/plugins/anchor/plugin.js";
import "tinymce/plugins/searchreplace/plugin.js";
import "tinymce/plugins/visualblocks/plugin.js";
import "tinymce/plugins/code/plugin.js";
import "tinymce/plugins/fullscreen/plugin.js";
import "tinymce/plugins/insertdatetime/plugin.js";
import "tinymce/plugins/table/plugin.js";
import "tinymce/plugins/contextmenu/plugin.js";
import "tinymce/plugins/paste/plugin.js";
import "tinymce/plugins/textcolor/plugin.js";
import "tinymce/plugins/colorpicker/plugin.js";

export default {
	data() {
		return {
			id: "tinymce__" + Date.now(),
			// 
			imgUrlId: "",
			// 
            format: ['image/jpg', 'image/jpeg', 'image/png'],
		};
	},
	props: ["value", "width", "height", "menubar", "toolbar", "placeholder"],
	watch: {
		value(val) {
			// 
			tinymce.get(this.id).setContent(val);
		}
	},
	mounted() {
		tinymce.init({
			selector: `#${this.id}`,
			height: this.height || 300,
			width: this.width || "100%",
			valid_children: "+body[style]",
			skin_url: "https://cdn.bootcss.com/tinymce/4.5.8/skins/lightgray",
			menubar: this.menubar === undefined ? true : this.menubar,
			// 
			plugins: [
				"advlist autolink lists link image charmap print preview anchor",
				"searchreplace visualblocks code fullscreen",
				"insertdatetime table contextmenu paste code textcolor colorpicker",
				"placeholder"
			],
			// 
			placeholder: this.placeholder || '',
			placeholder_attrs: { style: {position: 'absolute', top:'5px', left:0, color: '#bbbec8', padding: '1%', width:'98%', overflow: 'hidden', 'white-space': 'pre-wrap'} },
			// 
			toolbar: this.toolbar || "insertfile undo redo | styleselect | fontsizeselect | bold italic underline | forecolor backcolor | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | image link | code | preview fullscreen ",
			// 
			file_browser_callback: (field_name, url, type, win) => {
				if (type == "file") {
					this.$Message.warning('');
				}
				if (type == "image") {
					// this.$Message.warning('');
					this.$refs.imgFile.click();
					this.imgUrlId = field_name;
				}
			},
			//TinyMCE  font  span 
			convert_fonts_to_spans: true,
			// br 
			convert_newlines_to_brs: false,
			// TinyMCE  BR 
			force_br_newlines: false,
			// Mozilla/Firefox /
			//force_p_newlines: false,
			// HTML  textarea 
			//remove_linebreaks: false,
			//
			relative_urls: false,
			//
			resize: true,
			//font_formats: "=;=;=;=;=;=;=;Arial=arial,helvetica,sans-serif;Comic Sans MS=comic sans ms,sans-serif;Courier New=courier new,courier;Tahoma=tahoma,arial,helvetica,sans-serif;Times New Roman=times new roman,times;Verdana=verdana,geneva;Webdings=webdings;Wingdings=wingdings,zapf dingbats",
			fontsize_formats: "8px 10px 12px 14px 18px 24px 36px",
			setup: editor => {
				// 
				editor.on("init", () => {
					// 
					editor.setContent(this.value || "");
					// 
					editor.on("blur undo redo", () => {
						// 
						//this.setCon=editor.getContent();
						this.$emit("input", editor.getContent());
					});
					editor.on("blur", () => {
						this.$emit("blur");
					});
				});
			}
		});
	},
	methods: {
		// 
        selectFile(event){
            // 
            const fileList = this.$refs.imgFile.files;
            if(fileList.length > 0){
                let fileName = fileList[0].name;
                // kb
                let fileSize = Math.floor(fileList[0].size / 1024);

                let maxSize = 2048;
                let overHint = maxSize + 'kb';
                // 2M
                if(fileSize > maxSize){
                    if(maxSize > 1024) overHint = Math.floor(maxSize/1024) + 'M';
                    this.$Notice.warning({
                        title: '',
                        desc: ' ' + fileName + '  ' + overHint
                    });
                    this.progressHide();
                    return false;
                }
                this.uploadFile(fileList[0]);
            }
            else console.log('');
        },
		// 
		uploadFile(file){
			var thisFile = Bmob.File(file.name, file);
            
			thisFile.save().then(res => {
				let inputImgUrl = document.getElementById(this.imgUrlId);
				inputImgUrl.value = res[0].url;
				//change
				if (document.createEventObject) {
					// IEfireEvent
					let evt = document.createEventObject();
					inputImgUrl.fireEvent("onchange", evt);
				}
				else {
					// dispatchEvent
					let evt = document.createEvent("HTMLEvents");
					evt.initEvent("change", true, true);
					inputImgUrl.dispatchEvent(evt);
				}            
				this.$Notice.success({ title: '!' });
			}, err =>  {
				this.$Notice.error({ title: '!' });
				console.log(err);
			});
		}
	},
	beforeDestroy() {
		tinymce.remove(this.id);
	}
};
</script>
<style lang="less">
.m-tinymce > div {
	box-sizing: border-box;
}
.mce-window {
	z-index: 1001 !important;
}
#mce-modal-block {
	z-index: 1000 !important;
}
.tinymce__modalProgress {
	.ivu-modal-mask,
	.ivu-modal-wrap {
		z-index: 1010;
	}
}
.mce-fullscreen {
	.header,
	.side-bar {
		visibility: hidden;
	}
}
.ivu-form-item-error .mce-tinymce {
	border-color: #ed3f14;
}
.mce-branding {
	display: none !important;
}
</style>
